@import 'assets/scss/utils/mixins';

.container {
	padding: 24px 16px;
}

.close-btn {
	width: 32px;
	height: 32px;
	border: none;
	border-radius: 100px;

	mat-icon {
		font-size: 16px;
		height: 16px;
		width: 16px;
		margin-inline: auto;
	}
}

.dialog-header {
	min-height: 40px;
	gap: 16px;
}

.mt-4px {
	margin-top: 4px !important;
}

.info-container {
	gap: 4px;
	padding: 8px;
	border: 1px solid var(--info-2);
	border-radius: 4px;
	margin-top: 16px;
	margin-bottom: 16px;

	mat-icon {
		font-size: 17px;
		width: 17px;
		height: 17px;
	}
}

.lead-info-container {
	border: 1px solid var(--general-2);
	border-radius: 5px;
	padding: 16px;
}

.locked-lead {
	width: 100%;
	height: 100%;
	max-height: 233px;
}
